</style>

<script>
import { AccountAPI, DeviceAPI, PackageAPI, StatisticsAPI } from "@/scripts/ApiUtil"
import UserRole from "../../../server/UserRole"
import Nav from "@/components/Nav"
import Statcard from "@/components/Statcard"

import DeviceStrings from "../../../deviceStrings.json"
import DeviceVariants from "../../../deviceVariants.json"

export default {
	name: 'dashboard',
	components: {
		Nav,
		Statcard
	},
	data() {
		return {
			packageData: null,
			reviewData: null,
			statisticsData: null,
			deviceData: null,
			dataPoints: {
				downloads: { raw: [] },
				reviews: { raw: [] },
				accountCreations: { raw: [] },
				logins: { raw: [] },
				packageUploads: { raw: [] },
				versionUploads: { raw: [] }
			},
			
			accountData: {},
			isAdministrator: false,
			isModerator: false,
			isDeveloper: false,
			
			DeviceStrings: DeviceStrings,
			DeviceVariants: DeviceVariants
		}
	},
	async created() {		
		let _accountData = await AccountAPI.getMe(this.$cookies.get("authToken"));
		
		if (_accountData.status == 200) {
			this.accountData = _accountData.data;
			this.$store.commit('setAccountId', this.accountData.id);
			
			this.isAdministrator = (this.accountData.role & UserRole.ADMINISTRATOR) == UserRole.ADMINISTRATOR;
			this.isModerator = (this.accountData.role & UserRole.MODERATOR) == UserRole.MODERATOR;
			this.isDeveloper = (this.accountData.role & UserRole.DEVELOPER) == UserRole.DEVELOPER;
			
			let _reviewData = await PackageAPI.getReviews();
			if (_reviewData.status == 200) {
				this.reviewData = _reviewData.data;
			} else {
				this.reviewData = {
					status: _reviewData.status
				}
			}
			
			let _packageData = await PackageAPI.getPackages();
			if (_packageData.status == 200) {
				if (this.isAdministrator || this.isModerator) {
					this.packageData = _packageData.data.sort((a, b) => b.downloadCount > a.downloadCount);
				} else if (this.isDeveloper) {
					this.packageData = _packageData.data.filter(p => p.accountId == this.accountData.id).sort((a, b) => b.downloadCount > a.downloadCount);
				} else {
					if (!this.reviewData.status) {
						let filteredPackageIds = this.reviewData.map(r => r.packageId).filter((value, index, self) => { return self.indexOf(value) === index });
						this.packageData = _packageData.data.filter(p => filteredPackageIds.includes(p.id));
					} else {
						this.packageData = [];
					}
				}
			}
			
			let _deviceData = await DeviceAPI.getDevices();
			if (_deviceData.status == 200) {
				this.deviceData = _deviceData.data;
			} else {
				this.deviceData = {
					status: _deviceData.status
				}
			}
			
			let _statisticsData = await StatisticsAPI.getStatistics();
			if (_statisticsData.status == 200) {
				let statisticsData = _statisticsData.data;
				
				statisticsData.weeks.forEach(week => {
					Object.keys(this.dataPoints).forEach(key => {
						this.dataPoints[key].raw.push(week[key]);
					});
				});
				
				Object.keys(this.dataPoints).forEach(key => {
					let currentData = this.dataPoints[key].raw.lastObject();
					let previousData = this.dataPoints[key].raw.length > 1 ? this.dataPoints[key].raw.lastNthObject(1) : undefined;
					
					Object.assign(this.dataPoints[key], {
						current: currentData,
						previous: previousData,
						delta: (currentData - previousData)
					});
				});
			}
		} else {
			this.$store.commit('setAccountId', undefined);
			this.$router.replace("/");
		}
		
		setTimeout(function() {
			$(document).trigger("redraw.bs.charts");
		});
	},
	mounted() {
		window.scrollTo(0,0);
	},
	methods: {
		getDeviceVariantArtwork(deviceObj) {
			let deviceVariant = null;
			Object.keys(DeviceVariants).forEach(variant => {
				if (new RegExp(variant).test(deviceObj.product)) {
					let _variant = DeviceVariants[variant];
					let _deviceVariant = _variant.variants.find(_ => _.variant === deviceObj.variant);
					
					if (_variant && _deviceVariant) {
						deviceVariant = `${_variant.name}-${_deviceVariant.variant}_64x64@2x.png`;
						return;
					}
					
					if (_variant.default != "") {
						deviceVariant = `${_variant.name}-${_variant.default}_64x64@2x.png`;
						return;
					}
					
					deviceVariant = `${_variant.name}_64x64@2x.png`;
					return;
				}
			});
			
			return deviceVariant;
		},
		getDeviceVariantName(deviceObj) {
			let deviceVariant = null;
			Object.keys(DeviceVariants).forEach(variant => {
				if (new RegExp(variant).test(deviceObj.product)) {
					let _variant = DeviceVariants[variant];
					let _deviceVariant = _variant.variants.find(_ => _.variant === deviceObj.variant);
					
					if (_variant && _deviceVariant) {
						deviceVariant = _deviceVariant.name;
						return;
					}
					
					if (_variant.default != "") {
						_deviceVariant = _variant.variants.find(_ => _.variant === _variant.default);
						deviceVariant = _deviceVariant.name;
						return;
					}
					
					deviceVariant = "Unknown";
					return;
				}
			});
			
			return deviceVariant;
		}
	},
	filters: {
		number(value) {
			return new Intl.NumberFormat("en-US").format(value)
		}
	}
}
</script>