<template>
	<div :class="`statcard statcard-${color}`">
		<div class="p-3">
			<span class="statcard-desc">{{ name }}</span>
			<h2 class="statcard-number" v-if="typeof data.current === 'undefined'">
				<div class="loading-indicator" />
			</h2>
			<h2 class="statcard-number" v-if="data.current >= 0">
				
				<span>{{ data.current | number }}</span>
				
				<small class="delta-indicator" v-if="!isNaN(data.delta)" :class="{ 'delta-positive': data.delta > 0, 'delta-negative': data.delta < 0}">
					{{ data.delta == 0 ? "±" : data.delta > 0 ? "+" : "-"}} {{ Math.abs(data.delta) }}
				</small>
			</h2>
			<hr class="statcard-hr m-b-0">
		</div>
		<canvas width="387" height="94" class="sparkline" data-chart="spark-line" :data-dataset="`[[${data.raw}]]`" :data-labels="`[${data.raw}]`" style="width: 189px; height: 47px"></canvas>
	</div>
</template>

<script>
export default {
	name: "Statcard",
	props: ["data", "name", "color"],
	filters: {
		number(value) {
			return new Intl.NumberFormat("en-US").format(value)
		}
	}
}
</script>